$clockface-size: 400px;
$little-circle-size: 25px;
$border-radius: 50%;

$left-move: 0.49 * $clockface-size;

$top-move-minutes: 0.25 * $clockface-size;
$top-move-seconds: 0.2 * $clockface-size;

$height-minutes: $top-move-minutes;
$height-seconds: 0.3 * $clockface-size;

$width-minutes: 0.03 * $clockface-size;
$width-seconds: 0.02 * $clockface-size;
